@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  * {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', sans-serif, Arial;
  }
  h1 {
    @apply text-3xl font-bold
  }
  h2 {
    @apply text-2xl font-bold
  }
  h3 {
    @apply text-xl font-bold
  }
  h4 {
    @apply text-lg font-bold
  }
  h5 {
    @apply text-sm font-bold
  }
}